  }
  body[data-theme=auto] .aplayer .aplayer-info .aplayer-controller .aplayer-time .aplayer-icon path {
    fill: var(--color-font) !important;
  }
  body[data-theme=auto] .aplayer .aplayer-info .aplayer-controller .aplayer-time .aplayer-icon:hover path {
    fill: var(--color-focus) !important;
  }
  body[data-theme=auto] .aplayer .aplayer-list ol li {
    border-top: 1px solid var(--color-bg) !important;
  }
  body[data-theme=auto] .aplayer.aplayer-withlist .aplayer-info {
    border-bottom: 1px solid var(--color-bg) !important;
  }
  body[data-theme=auto] .aplayer .aplayer-list ol li.aplayer-list-light {
    background-color: var(--color-codebg) !important;
  }
  body[data-theme=auto] .aplayer .aplayer-list ol li:hover {
    background-color: var(--color-codebg) !important;
  }
  body[data-theme=auto] .aplayer .aplayer-list ol li .aplayer-list-author,
body[data-theme=auto] .aplayer .aplayer-list ol li .aplayer-list-index {
    color: var(--color-font) !important;
  }
}

body[data-theme=light] {
  --color-mode: "light";
  --color-primary: #8aa2d3;
  --color-secondary: #8f82bc;
  --color-focus: #3b469b;
  --color-mute: #9ea1a3;
  --color-font: #3f4551;
  --color-divider: #e5e2e4;
  --color-bg: #fffffd;
  --color-codebg: #f6f8fa;
}

body[data-theme=dark] {
  --color-mode: "dark";
  --color-primary: #8aa2d3;
  --color-secondary: #bab1df;
  --color-focus: #e6e6e6;
  --color-mute: #9ea1a3;
  --color-font: #c0c0c0;
  --color-divider: #4d5158;
  --color-bg: #2f3136;
  --color-codebg: #414449;
}
body[data-theme=dark] table th,
body[data-theme=dark] table td {
  border-color: var(--color-font);
}
body[data-theme=dark] table tr {
  background-color: var(--color-bg);
  border-color: var(--color-font);
}
body[data-theme=dark] table tr:nth-child(2n) {
  background-color: var(--color-divider);
}
body[data-theme=dark] #post-aplayer {
  background-color: var(--color-divider);
}
body[data-theme=dark] .aplayer .aplayer-info .aplayer-music .aplayer-author {
  color: var(--color-font) !important;
}
body[data-theme=dark] .aplayer .aplayer-info .aplayer-controller .aplayer-time .aplayer-icon path {
  fill: var(--color-font) !important;
}
body[data-theme=dark] .aplayer .aplayer-info .aplayer-controller .aplayer-time .aplayer-icon:hover path {
  fill: var(--color-focus) !important;
}
body[data-theme=dark] .aplayer .aplayer-list ol li {
  border-top: 1px solid var(--color-bg) !important;
}
body[data-theme=dark] .aplayer.aplayer-withlist .aplayer-info {
  border-bottom: 1px solid var(--color-bg) !important;
}
body[data-theme=dark] .aplayer .aplayer-list ol li.aplayer-list-light {
  background-color: var(--color-codebg) !important;
}
body[data-theme=dark] .aplayer .aplayer-list ol li:hover {
  background-color: var(--color-codebg) !important;
}
body[data-theme=dark] .aplayer .aplayer-list ol li .aplayer-list-author,
body[data-theme=dark] .aplayer .aplayer-list ol li .aplayer-list-index {
  color: var(--color-font) !important;
}

::selection,
::-moz-selection {
  color: var(--color-bg);
  background-color: var(--color-primary);
}

body {
  background-color: var(--color-bg);
  color: var(--color-font);
}
body a {
  color: var(--color-primary);
}
body a:hover {
  color: var(--color-secondary);
}

.post-summary a,
.post-meta a {
  color: var(--color-secondary);
}
.post-summary a:hover,
.post-meta a:hover {
  color: var(--color-focus);
}

.pag-current a,
.pag-current a:hover {
  color: var(--color-focus);
}

.title-sub,
.post-meta {
  color: var(--color-mute);
}

header,
.page-info {
  border-bottom: 2px solid var(--color-divider);
}

.post + .post,
.pagination,
footer {
  border-top: 2px solid var(--color-divider);
}

.post-meta-archive {
  color: var(--color-mute);
}
.post-meta-archive a {
  color: var(--color-secondary);
}
.post-meta-archive a:hover {
  color: var(--color-focus);
}

@media screen and (max-width: 768px) {
  .sidebar {
    border-top: 2px solid var(--color-divider);
  }
}
.post-content a {
  color: var(--color-secondary);
}
.post-content a:hover {
  color: var(--color-focus);
}

.markdown-body h1,
.markdown-body h2,
.markdown-body h3,
.markdown-body h4,
.markdown-body h5,
.markdown-body h6 {
  color: var(--color-primary);
}
.markdown-body strong,
.markdown-body em {
  color: var(--color-secondary);
}
.markdown-body h1,
.markdown-body h2 {
  border-bottom: 2px solid var(--color-divider);
}
.markdown-body blockquote {
  color: var(--color-mute);
}
.markdown-body img {
  border-radius: 2px;
  background-color: inherit;
}
.markdown-body hr {
  height: 2px;
}
.markdown-body pre,
.markdown-body code {
  background-color: var(--color-codebg);
}

.post-loading {
  border-top: 2px solid var(--color-divider);
}

.post-comment {
  border-top: 2px solid var(--color-divider);
}
.post-comment span.post-comment-notloaded {
  padding: 1.5rem 0;
  color: var(--color-secondary);
  cursor: pointer;
}
.post-comment span.post-comment-notloaded:hover {
  color: var(--color-focus);
}

.btn div {
  background-color: var(--color-divider);
  color: var(--color-primary);
}
.btn div:hover {
  background-color: var(--color-primary);
  color: var(--color-divider);
}
.btn div + div {
  border-top: 2px solid var(--color-bg);
}

code[class*=language-],
pre[class*=language-] {
  -moz-tab-size: 4;
  -o-tab-size: 4;
  tab-size: 4;
}

.token.comment,
.token.prolog,
.token.doctype,
.token.cdata {
  color: #93a1a1;
  /* base1 */
}

.token.punctuation {
  color: #586e75;
  /* base01 */
}

.token.namespace {
  opacity: 0.7;
}

.token.property,
.token.tag,
.token.boolean,
.token.number,
.token.constant,
.token.symbol,
.token.deleted {
  color: #268bd2;
  /* blue */
}

.token.selector,
.token.attr-name,
.token.string,
.token.char,
.token.builtin,
.token.url,
.token.inserted {
  color: #2aa198;
  /* cyan */
}

.token.entity {
  color: #657b83;
  /* base00 */
  background: #eee8d5;
  /* base2 */
}

.token.atrule,
.token.attr-value,
.token.keyword {
  color: #859900;
  /* green */
}

.token.function,
.token.class-name {
  color: #b58900;
  /* yellow */
}

.token.regex,
.token.important,
.token.variable {
  color: #cb4b16;
  /* orange */
}

.token.important,
.token.bold {
  font-weight: 600;
}

.token.italic {
  font-style: italic;
}

pre .rule,
code .rule {
  margin: 0;
  border-bottom: none;
  overflow: inherit;
}
pre .rule::before, pre .rule::after,
code .rule::before,
code .rule::after {
  display: inherit;
  clear: inherit;
}

#dsqjs a {
  text-decoration: none !important;
  color: var(--color-secondary) !important;
}
#dsqjs a:hover {
  color: var(--color-focus) !important;
}
#dsqjs .dsqjs-bullet::after {
  color: var(--color-mute) !important;
  font-weight: 600 !important;
}
#dsqjs .dsqjs-no-comment {
  color: var(--color-font) !important;
}
#dsqjs .dsqjs-nav-tab {
  color: var(--color-mute) !important;
}
#dsqjs .dsqjs-tab-active {
  color: var(--color-font) !important;
}
#dsqjs .dsqjs-tab-active > span:after {
  background-color: var(--color-secondary) !important;
}
#dsqjs .dsqjs-post-list .dsqjs-post-avatar {
  background: var(--color-bg) !important;
}
#dsqjs .dsqjs-post-list .dsqjs-post-header .dsqjs-post-author {
  color: var(--color-mute) !important;
  font-weight: 600;
}
#dsqjs .dsqjs-post-list .dsqjs-post-header .dsqjs-admin-badge {
  color: var(--color-font) !important;
  background: var(--color-codebg) !important;
}
#dsqjs .dsqjs-post-list .dsqjs-post-header .dsqjs-meta {
  color: var(--color-mute) !important;
}
#dsqjs .dsqjs-post-body {
  color: var(--color-font) !important;
}
#dsqjs .dsqjs-post-body code,
#dsqjs .dsqjs-post-body pre {
  background: var(--color-codebg) !important;
}
#dsqjs .dsqjs-post-body blockquote {
  color: var(--color-codebg) !important;
  border-left: 0.25em solid var(--color-divider) !important;
}
#dsqjs .dsqjs-load-more {
  background: var(--color-mute) !important;
  color: var(--color-bg) !important;
}
#dsqjs footer {
  border-top: 2px solid var(--color-divider) !important;
  font-weight: 600 !important;
  color: var(--color-mute) !important;
}
#dsqjs .dsqjs-order-radio:checked + .dsqjs-order-label {
  color: var(--color-font) !important;
  background-color: var(--color-divider) !important;
}
#dsqjs .dsqjs-order-label {
  background-color: var(--color-bg) !important;
}
#dsqjs p.dsqjs-has-more a.dsqjs-has-more-btn {
  color: var(--color-mute) !important;
}

.search-result-info {
  border-bottom: 2px solid var(--color-divider);
}

.search-input {
  border-top: 2px solid;
  border-left: 2px solid;
  border-bottom: 2px solid;
  border-right: none;
  border-radius: 0.25rem;
  border-color: var(--color-divider);
  background-color: var(--color-bg);
}
.search-input:hover, .search-input:focus-within {
  color: var(--color-primary);
  border-color: var(--color-primary);
}
.search-input input {
  outline: none;
  border: none;
  color: inherit;
  background-color: inherit;
}
.search-input button {
  outline: none;
  border-top: 2px solid;
  border-left: none;
  border-right: 2px solid;
  border-bottom: 2px solid;
  border-radius: 0 0.25rem 0.25rem 0;
  border-color: inherit;
  color: inherit;
  background-color: inherit;
}
.search-input button:hover {
  color: var(--color-secondary);
}